.App {
  max-width: 700px;
  margin: 50px auto;
}

.todo-list-app {}
.todo-list {}

.todo-item {
  display: flex;
  align-items: center;
  padding: 10px;
  margin: 3px;
  border: 2px solid silver;
}
.todo-item__part {
  margin-right: 10px;
}

.todo-item__part:last-child {
  margin-right: 0;
  margin-left: auto;
  cursor: pointer;
  font-weight: bold;
}

.todo-item__part:last-child:hover {
  border-color: tomato;
  color: tomato;
}